import React, { useState, useEffect } from "react";
import { getDatabase, ref, update, get, remove } from "firebase/database";
import { useDispatch, useSelector } from "react-redux";
import { setWords, setPart } from "../redux/slices/wordsSlice";
import data from "../words.json";
import SearchForm from "../components/SearchForm";

function Words() {
  const [displayCount, setDisplayCount] = useState(20);

  const { userUid, words, part, searchWord } = useSelector(
    (state) => state.words
  );
  const dispatch = useDispatch();

  useEffect(() => {
    const db = getDatabase();
    const userWordsRef = ref(db, `users/${userUid}/words`);
    get(userWordsRef).then((snapshot) => {
      if (snapshot.exists()) {
        const wordsData = snapshot.val();
        if (Array.isArray(wordsData)) {
          dispatch(setWords(wordsData));
        }
      }
    });
  }, [userUid, dispatch]);

  const writeUserData = (wordsArr) => {
    const db = getDatabase();
    const userWordsRef = ref(db, `users/${userUid}/words`);
    const wordsObj = wordsArr.reduce((acc, cur, i) => {
      if (cur) {
        acc[i] = {
          ...cur,
        };
      }
      return acc;
    }, {});
    update(userWordsRef, wordsObj);
  };

  // 
  const handleAddWord = (wordId) => {
    const currentDate = new Date().toLocaleDateString("uk-UA");
    const existingWord = words.find((w) => w.id === wordId);
 
    if (existingWord && existingWord.dateAdded) {
   /*   console.log(existingWord.dateAdded);
      //   ,   
      const newWord = {
        id: wordId,
        dateAdded: existingWord.dateAdded,
      };
      console.log('newWord', newWord);
      const updatedWords = [...words.filter((w) => w.id !== wordId), newWord];
      dispatch(setWords(updatedWords));
      writeUserData(updatedWords); */
      writeUserData(words); 
    } else {
      //   ,    
      const newWord = {
        id: wordId,
        dateAdded: currentDate,
      };
      const updatedWords = [...words, newWord];
      dispatch(setWords(updatedWords));
      writeUserData(updatedWords);
    }
  };

  // 
  const handleDeleteWord = (wordId) => {
    const db = getDatabase();
    const userWordsRef = ref(db, `users/${userUid}/words`);
    console.log('words',words);
    remove(userWordsRef)
      .then(() => {
        const filteredWords = words.filter((word) => word.id !== wordId);
        dispatch(setWords(filteredWords));
        console.log(filteredWords);
        writeUserData( filteredWords);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  //    
  const handleSelectChange = (event) => {
    dispatch(setPart(event.target.value)); //      
  };


  //    ///
  const handleScroll = () => {
    const windowHeight = window.innerHeight;
    const documentHeight = document.documentElement.scrollHeight;
    const scrollTop = document.documentElement.scrollTop;
  
    if (scrollTop + windowHeight >= documentHeight) {
      setDisplayCount(displayCount + 20);
    }
  };

  
  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, [displayCount]);
  
  



  //      
  const filterWords = () => {
  const filteredWords = data.result.filter((item) => {
    const matchesSearch = item.englishWord.includes(searchWord.toLowerCase());
    const matchesPart = part === "" || item.partsOfSpeech === part;
    return matchesSearch && matchesPart;
  });

  return filteredWords.slice(0, displayCount);
};


useEffect(()=>{
  filterWords();
  }, [part, searchWord, displayCount])




  return (
    <div>
      <h1> 1000     </h1>
      {userUid === 'none' &&  <p>          , </p>}
     
      <br></br>
      <SearchForm></SearchForm>
      <div className="table">
        <table>
          <thead>
            <tr>
              <th> </th>
              <th></th>
              <th>
                {" "}
                <div>
                  <label>  :</label>
                  <select value={part} onChange={handleSelectChange}>
                    <option value=""></option>
                    <option value="noun"></option>
                    <option value="verb"></option>
                    <option value="adjective"></option>
                    <option value="adverb"></option>
                    <option value="phrase"></option>
                    <option value="interjection"> </option>
                  </select>
                </div>
              </th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {part === "" && searchWord === ""
              ? filterWords().map((word) => (
                  <tr key={word.id}>
                    <td>{word.englishWord}</td>
                    <td>{word.translateWord}</td>
                    <td>{word.partsOfSpeech}</td>
                    <td>{word.englishPronunciation}</td>
                    <td>
                      <button onClick={() => handleAddWord(word.id)}>
                        Add
                      </button>
                    </td>

                    <td>
                      <button onClick={() => handleDeleteWord(word.id)}>
                        Del
                      </button>
                    </td>
                    {words.find((w) => w.id === word.id) ? (
                      <td>&#10004;</td>
                    ) : (
                      <td></td>
                    )}
                  </tr>
                ))
              : filterWords() &&
                filterWords().map((word) => (
                  <tr key={word.id}>
                    <td>{word.englishWord}</td>
                    <td>{word.translateWord}</td>
                    <td>{word.partsOfSpeech}</td>
                    <td>{word.englishPronunciation}</td>
                    <td>
                      <button onClick={() => handleAddWord(word.id)}>
                        Add
                      </button>
                    </td>

                    <td>
                      <button onClick={() => handleDeleteWord(word.id)}>
                        Del
                      </button>
                    </td>
                    {words.find((w) => w.id === word.id) ? (
                      <td>&#10004;</td>
                    ) : (
                      <td></td>
                    )}
                  </tr>
                ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default Words;
